.list {
    display: flex;
    flex-wrap: wrap;
    width: 35%;
    &__wrap {
        display: flex;
        gap: 10px;
        margin-bottom: 10px;
    }
    a {
        img {
            display: block;
            width: 100%;
            height: auto;
        }
    }
    .piece-wrap {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
    .btn-down-all {
        width: 100%;
    }
}
@media (max-width: 767px) {
    .list {
        width: 100%;
    }
}
